import React, { useState } from "react";
import { useSelector } from "../../utils/hooks";
import styles from './appHeader.module.css';
import { Logo, BurgerIcon, ListIcon, ProfileIcon } from 
  '@ya.praktikum/react-developer-burger-ui-components';
import LinkHeader from '../linkHeader/linkHeader';
import { Link } from 'react-router-dom';

export interface IAppHeaderState {
  '': boolean;
  ' ': boolean;
  ' ': boolean;
}

function AppHeader() {

  const activePage: string = useSelector((state) => state.app.activePage);

  const [isLinkHover, setIsLinkHover] = useState<IAppHeaderState> ({
                                                   '': false,
                                                   ' ': false,
                                                   ' ': false
                                                 });

  const toggleIsHover = (newState: IAppHeaderState): void => {
    setIsLinkHover({
      ...newState
    });
  }

  return (
    <header className={styles.header}>
      <nav className={styles.navigation}>
        <LinkHeader 
          textColor={activePage === 'constructor' || isLinkHover[''] ? 
            'active' : 'inactive'}
          icon= {<BurgerIcon 
                    type={activePage === 'constructor' || isLinkHover[''] ? 
                      "primary" : "secondary"}
                 />}
          state={isLinkHover}
          changeIsHover={toggleIsHover}
          to='/'
          >
          
        </LinkHeader>
        <LinkHeader 
          textColor={activePage === 'orders' || isLinkHover[' '] ? 
            'active' : 'inactive'}
          icon={<ListIcon type={activePage === 'orders' || isLinkHover[' '] ? 
            "primary" : "secondary"}/>} 
          state={isLinkHover}
          changeIsHover={toggleIsHover}
          to='/feed'
          >
           
        </LinkHeader>
      </nav>
      <Link to='/'>
        <Logo />
      </Link>
      <nav className={styles.accountLink}>
        <LinkHeader 
          textColor={activePage === 'account' || isLinkHover[' '] ? 
            'active' : 'inactive'}
          icon={<ProfileIcon type={activePage === 'account' || 
            isLinkHover[' '] ? "primary" : "secondary"}/>}
          state={isLinkHover}
          changeIsHover={toggleIsHover}
          to='/profile'
          >
           
        </LinkHeader>
      </nav>
    </header>
  );
}

export default AppHeader;
